import axios from 'axios'
import { Message } from 'element-ui'

// axios
const service = axios.create({
  baseURL: process.env.VUE_APP_BASE_API || '', // APIbase_url
  timeout: 60000, // 
  withCredentials: true // cookie
})

// 
service.interceptors.request.use(
  config => {
    // token
    const token = localStorage.getItem('token')
    if (token) {
      config.headers['Authorization'] = `Bearer ${token}`
    }
    return config
  },
  error => {
    console.error('', error)
    return Promise.reject(error)
  }
)

// 
service.interceptors.response.use(
  response => {
    const res = response.data
    
    // 
    if (response.status === 200) {
      return res
    }
    
    // 
    Message({
      message: res.message || '',
      type: 'error',
      duration: 5 * 1000
    })
    
    // 
    if (res.code === 401) {
      // token
      localStorage.removeItem('token')
      window.location.href = '/login'
    }
    
    return Promise.reject(new Error(res.message || ''))
  },
  error => {
    console.error('', error)
    Message({
      message: error.message,
      type: 'error',
      duration: 5 * 1000
    })
    return Promise.reject(error)
  }
)

// GET
const get = (url, params) => {
  return service({
    url,
    method: 'get',
    params
  })
}

// POST
const post = (url, data) => {
  return service({
    url,
    method: 'post',
    data
  })
}

// PUT
const put = (url, data) => {
  return service({
    url,
    method: 'put',
    data
  })
}

// DELETE
const remove = (url, data) => {
  return service({
    url,
    method: 'delete',
    data
  })
}

export default {
  get,
  post,
  put,
  delete: remove
} 