import Request from '@/utils/request'

/**
 * start
 * @param {Object} data 
 * @returns {Promise} Promise
 */
export function getStartTextToImage(data) {
  // 
  const requestData = {
    prompt: data.prompt,
    aspectRatio: 'square', 
    imgCount: data.imgCount,
    imageSize: {
      width: data.width,
      height: data.height
    },
    userId: 1
  }
  
  return Request.post('/api/image/start/text-to-image', requestData)
} 