/**
 * 
 * @param {Object} params 
 * @returns {Number} 
 */
export function calculateTokens(params) {
  const { 
    width = 512, 
    height = 512, 
    steps = 30, 
    batchSize = 1, 
    upscale = false, 
    faceRestore = false 
  } = params;
  
  // 
  const baseArea = 512 * 512;
  const currentArea = width * height;
  const areaRatio = currentArea / baseArea;
  
  // 
  let tokens = Math.ceil(areaRatio * steps * batchSize);
  
  // 
  if (upscale) {
    tokens += Math.ceil(tokens * 0.5);
  }
  
  // 
  if (faceRestore) {
    tokens += Math.ceil(tokens * 0.2);
  }
  
  return tokens;
}

/**
 * 
 * @returns {Number} 
 */
export function generateRandomSeed() {
  return Math.floor(Math.random() * 2147483647);
}

/**
 * 
 * @param {Number} saturation  (0-100)
 * @param {Number} lightness  (0-100)
 * @returns {String} HSL
 */
export function generateRandomColor(saturation = 70, lightness = 80) {
  const hue = Math.floor(Math.random() * 360);
  return `hsl(${hue}, ${saturation}%, ${lightness}%)`;
}

/**
 * 
 * @param {String} prompt 
 * @returns {String} 
 */
export function enhancePrompt(prompt) {
  if (!prompt.trim()) return prompt;
  
  // 
  let enhanced = prompt.trim();
  
  // 
  if (!/[.,!?]$/.test(enhanced)) {
    enhanced += '.';
  }
  
  return enhanced;
}

/**
 * Stable Diffusion
 * @param {String} infoText 
 * @returns {Object} 
 */
export function parseImageInfo(infoText) {
  if (!infoText) return {};
  
  const result = {};
  
  // 
  const promptMatch = infoText.match(/^(.*?)(?:Negative prompt:|$)/s);
  if (promptMatch) {
    result.prompt = promptMatch[1].trim();
  }
  
  const negPromptMatch = infoText.match(/Negative prompt:(.*?)(?:Steps:|$)/s);
  if (negPromptMatch) {
    result.negativePrompt = negPromptMatch[1].trim();
  }
  
  // 
  const paramsText = infoText.replace(/^.*?Negative prompt:.*?/s, '');
  const paramMatches = paramsText.matchAll(/(\w+):\s*([^,]+)(?:,|$)/g);
  
  for (const match of paramMatches) {
    const [, key, value] = match;
    result[key.trim()] = value.trim();
  }
  
  return result;
}

