/**
 * 
 * @param {Object} params 
 * @param {Function} progressCallback 
 * @returns {Promise<Array>} 
 */
export function simulateImageGeneration(params, progressCallback) {
  const { batchSize = 1, steps = 30 } = params;
  
  return new Promise((resolve) => {
    const images = [];
    let currentStep = 0;
    
    // 
    const interval = setInterval(() => {
      currentStep++;
      if (progressCallback) {
        progressCallback({
          step: currentStep,
          totalSteps: steps,
          percentage: Math.floor((currentStep / steps) * 100)
        });
      }
      
      // 
      if (currentStep >= steps) {
        clearInterval(interval);
        
        // 
        for (let i = 0; i < batchSize; i++) {
          const randomSeed = generateRandomSeed();
          images.push({
            id: `sim-${Date.now()}-${i}`,
            url: `https://picsum.photos/seed/${randomSeed}/512/512`, // 
            seed: randomSeed,
            prompt: params.prompt || '',
            params: { ...params, seed: randomSeed }
          });
        }
        
        resolve(images);
      }
    }, 100);
  });
}

